.footer {
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  height: 190px;
  width: 100%;

  &__copyright {
    color: white;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 20px;
    font-size: 25px;
  }

  @media (max-width: 768px) {

    &__copyright {
      width: 150px;
      margin-top: 10px;
      margin-bottom: 50px;
      font-size: 15px;
    }
  }
}
