.banner {
  margin: 20px 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  &__photo {
    display: flex;
    width: 100%;
    height: 230px;
    object-fit: cover;
    border-radius: 20px;
    box-shadow: 0px 5px 10px whitesmoke;
    filter: brightness(0.4);
  }

  &__photo--apropos {
    filter: brightness(0.8);
  }

  &__text {
    display: flex;
    position: absolute;
    font-weight: 700;
    font-size: 35px;
    color: white;
    font-family: "montserrat", sans-serif;
    margin-left: 10px
  }

  &__text--contour {
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
  }

  @media (max-width: 768px) {

    margin: 10px 15px;

    &__photo {
      height: 140px;
    }

    &__text {
      font-size: 20px;
    }

    &__text--contour {
      -webkit-text-stroke-width: 0;
    }
  }
}